<template>
    <div class="address">
        <van-nav-bar
        title="我的地址"
        left-text="返回"
        left-arrow
        @click-left="$router.go(-1)"
        />
        <van-empty image="search" description="空空如也" v-if="list.length===0" />
        <van-address-list
        v-model="chosenAddressId"
        :list="list"
        default-tag-text="默认"
        @add="onAdd"
        @edit="onEdit"
        @select="onSelect"
        />
    </div>
</template>

<script>
import { getAddressesListApi, setDefaultAddressApi, getDefaultAddressApi } from '@/api/addresses'
export default {
  name: 'addresses-vue',
  data () {
    return {
      show: true,
      chosenAddressId: '1',
      list: [],
      data: {}
    }
  },
  async created () {
    const res = await getAddressesListApi()
    const defaultAddress = await getDefaultAddressApi()
    // console.log('defaultAddressId:', defaultAddress)
    // console.log('addresses:', res)
    res.data.list.forEach(element => {
      const address = element.region.province + element.region.city + element.region.region + element.detail
      const { address_id: id, name, phone: tel } = element
      if (element.address_id === defaultAddress.data.defaultId) {
        this.list.push({ address, id, name, tel, isDefault: true })
      } else {
        this.list.push({ address, id, name, tel, isDefault: false })
      }
      // console.log({ address, id, name, tel })
    })
    // console.log('list:', this.list)
  },
  methods: {
    onAdd () {
      this.$router.push(`/addressDetail?address_id=${-1}`)
    },
    onEdit (item, index) {
      this.$router.push(`/addressDetail?address_id=${this.list[index].id}`)
    },
    async onSelect (item, index) {
      await setDefaultAddressApi(item.id)
      // console.log(item, index)
      this.list.forEach(element => {
        element.isDefault = false
      })
      this.list[index].isDefault = true
    }
  }
}
</script>

